export const formatRecord = (record) => {
	const {
		value,
		tag,
		date,
		time,
		remark,
		dinnerTime,
		dinnerFood
	} = record;
	const userId = uni.getStorageSync("user")._id;
	const result = {
		date,
		userId,
	};
	switch (tag) {
		case "":
			result.limosis = {
				value,
				time,
				remark
			}
			break;
		case "":
			result.afterBreakfast = {
				value,
				time,
				remark,
				dinnerTime,
				food: dinnerFood
			}
			break;
		case "":
			result.beforeLunch = {
				value,
				time,
				remark,
			}
			break;
		case "":
			result.afterLunch = {
				value,
				time,
				remark,
				dinnerTime,
				food: dinnerFood
			}
			break;
		case "":
			result.beforeDinner = {
				value,
				time,
				remark,
			}
			break;
		case "":
			result.afterDinner = {
				value,
				time,
				remark,
				dinnerTime,
				food: dinnerFood
			}
			break;
		case "":
			result.beforeSleep = {
				value,
				time,
				remark,
			}
			break;
		default:
			console.log("")
	}
	return result;
}